<script>
import { ref, watch, onMounted, onUnmounted } from "vue"
import NoData from "@/components/global/NoData.vue"

import CaregiverCard from "./components/CaregiverCard.vue"
import { apiGetCaretakerList, apiPostCaretakerList } from "@/api/api"
import SearchArea from "./components/SearchArea.vue"

import { isBetweenBottom } from "@/utils/helper"

import { Toast } from "@/components/global/swal"

import BackToTop from "@/components/global/BackToTop.vue"
import SaveWindowY from "@/components/global/SaveWindowY.vue"

export default {
  name: "caregiverList",
  setup() {
    const caretakersData = ref([])
    const nowPage = ref(0)
    let APIparams = ref({ page: 1, limit: 20 })
    let totalData = ref(0)
    let getApiTimer = null
    const canGetData = ref(true)

    onMounted(async () => {
      getApiTimer = setInterval(async () => {
        if (!isBetweenBottom()) return
        if (canGetData.value == false) {
          // console.log("冷卻中||資料都拿完了")
          return
        }
        if (APIparams.value.page === nowPage.value) {
          // console.log("頁數相同跳過")
          return
        }

        await handleGet(APIparams.value, { isConcat: true })
        APIparams.value.page += 1
      }, 500)
    })

    const updateParams = async (val) => {
      canGetData.value = true
      APIparams.value = val
      await handleGet(APIparams.value, { isReSearch: true })
    }

    const handleGet = async (
      param,
      config = { isConcat: false, isReSearch: false }
    ) => {
      if (canGetData.value == false) {
        // console.log("冷卻中||資料都拿完了")
        return
      }
      if (
        APIparams.value.page === nowPage.value &&
        config.isReSearch === false
      ) {
        // console.log("頁數相同跳過")
        return
      }
      // 先關閉
      canGetData.value = false
      // 請求開始

      const response = await apiPostCaretakerList(param)
      const { data, message, status } = response
      if (status) {
        // 成功
        canGetData.value = true
        nowPage.value = APIparams.value.page
        totalData.value = data.total

        if (config.isConcat) {
          caretakersData.value = [...caretakersData.value, ...data.data]
        } else {
          caretakersData.value = [...data.data]
        }

        if (caretakersData.value?.length <= 0) {
          // 啟動中斷機制
          canGetData.value = false
          return
        }
      } else {
        Toast(message)
      }
    }
    // //取消監控
    onUnmounted(() => clearInterval(getApiTimer))

    return { caretakersData, updateParams, APIparams, canGetData }
  },
  components: {
    CaregiverCard,
    SearchArea,
    NoData,
    BackToTop,
    SaveWindowY,
  },
}
</script>

<template>
  <div class="caregiver">
    <SaveWindowY />
    <section
      class="c-main"
      id="infoBox"
      style="padding-top: 48px; padding-bottom: 0px"
    >
      <SearchArea @params="updateParams" />
      <div class="caregiver-outer">
        <div>
          <NoData v-if="caretakersData.length == 0" />
          <div class="datalist">
            <CaregiverCard
              v-for="item in caretakersData"
              :item="item"
              :key="item.id"
            />
          </div>
        </div>
      </div>
    </section>
    <BackToTop />
  </div>
</template>

<style lang="scss" scoped>
@import "@/assets/custom/config";

.caregiver {
  // margin-bottom: calc($footer-height + 15px);
}
</style>
